import "./styles.css";
import React from "react";
import Chart from "./PriceChart";
import RangeSlider from "./rangeSlider";
import ConfirmBox from "./Prompt";
import Dialog from "./Dialog";
import DebtDialog from "./DebtDialog";
import Alert from "./Alert";

export default function App() {
  let w_price = Math.floor(Math.random() * (850 - 25 + 1)) + 25;
  let p_price = Math.floor(Math.random() * (3050 - 880 + 1)) + 880;
  let c_price = Math.floor(Math.random() * (80000 - 30000 + 1)) + 30000;
  let h_price = Math.floor(Math.random() * (40000 - 5000 + 1)) + 5000;
  let a_price = Math.floor(Math.random() * (4500 - 900 + 1)) + 900;
  let l_price = Math.floor(Math.random() * (70 - 4 + 1)) + 4;
  const [priceChart, setPriceChart] = React.useState({
    cash: 10000,
    bank: 0,
    debt: 5500,
    items: [
      { name: "weed", price: w_price, qty: 0 },
      { name: "cocaine", price: c_price, qty: 0 },
      { name: "heroine", price: h_price, qty: 0 },
      { name: "acid", price: a_price, qty: 0 },
      { name: "ludes", price: l_price, qty: 0 },
      { name: "pcp", price: p_price, qty: 0 }
    ]
  });
  const [location, setLocation] = React.useState([
    "bankner",
    "narela",
    "bawana",
    "alipur",
    "azadpur"
  ]);
  const [currentArea, setCurrentArea] = React.useState("bankner");
  const [days, setDays] = React.useState(30);

  const [message, setMessage] = React.useState("");

  const [dialogContent, setDialogContent] = React.useState("");
  const [showDialog, setShowDialog] = React.useState(false);
  // Loan dialog
  const [debtDialogContent, setDebtDialogContent] = React.useState("");
  const [debtShowDialog, setDebtShowDialog] = React.useState(false);

  function handleLoanPay() {
    if (priceChart.cash > priceChart.debt) {
      setQtyMax(priceChart.debt);
    } else {
      setQtyMax(priceChart.cash);
    }
    setItemName("Pay Loan!");
    setAction("pay-debt");
    setShowSlider(true);
  }
  function handleLoanTake() {
    setQtyMax(500);
    setItemName("Take Loan");
    setAction("take-debt");
    setShowSlider(true);
  }
  function handleBankDeposit() {
    setQtyMax(priceChart.cash);
    setItemName("Deposit Cash!");
    setAction("Deposit");
    setShowSlider(true);
  }

  function handleBankWithDraw() {
    setQtyMax(priceChart.bank);
    setItemName("Withdraw Cash!");
    setAction("Withdraw");
    setShowSlider(true);
  }

  // hide alert
  function hideAlert() {
    setMessage("");
  }

  // Reset Function
  function reset_to_default() {
    setPriceChart({
      cash: 10000,
      bank: 0,
      debt: 5550,
      items: [
        { name: "weed", price: w_price, qty: 0 },
        { name: "cocaine", price: c_price, qty: 0 },
        { name: "heroine", price: h_price, qty: 0 },
        { name: "acid", price: a_price, qty: 0 },
        { name: "ludes", price: l_price, qty: 0 },
        { name: "pcp", price: p_price, qty: 0 }
      ]
    });
    setDays(10);
    setCurrentArea("bankner");
  }
  //

  function setPrices() {
    // setting prices
    let w_price = Math.floor(Math.random() * (850 - 25 + 1)) + 25;
    let p_price = Math.floor(Math.random() * (3050 - 880 + 1)) + 880;
    let c_price = Math.floor(Math.random() * (80000 - 30000 + 1)) + 30000;
    let h_price = Math.floor(Math.random() * (40000 - 5000 + 1)) + 5000;
    let a_price = Math.floor(Math.random() * (4500 - 900 + 1)) + 900;
    let l_price = Math.floor(Math.random() * (70 - 4 + 1)) + 4;
    priceChart.items.filter((p) => p.name === "weed")[0].price = w_price;
    priceChart.items.filter((p) => p.name === "cocaine")[0].price = c_price;
    priceChart.items.filter((p) => p.name === "heroine")[0].price = h_price;
    priceChart.items.filter((p) => p.name === "acid")[0].price = a_price;
    priceChart.items.filter((p) => p.name === "ludes")[0].price = l_price;
    priceChart.items.filter((p) => p.name === "pcp")[0].price = p_price;
  }

  function changeLocation(area) {
    if (currentArea === area) {
      setMessage(
        `You are already in ${area.replace(/^\w/, (c) => c.toUpperCase())}`
      );
      return false;
    }
    const items = priceChart.items;
    const cash = priceChart.cash;
    const debt = priceChart.debt;
    const bank = priceChart.bank;
    const new_debt = Math.round(debt + (debt * 10) / 100);
    const new_bank = Math.round(bank + (bank * 2) / 100);
    setPrices();
    setDays(days - 1);
    if (days <= 0) {
      let score = bank + cash - debt;
      setQtyMax(0);
      setMessage(`Game Over, Your Score is ${score}`);
      reset_to_default();
      return 0;
    }
    setPriceChart({
      cash: cash,
      debt: new_debt,
      bank: new_bank,
      items: [...items]
    });
    setCurrentArea(area);
  }

  // get max qty you can buy with limited cash
  function max_qty(price, cash) {
    let quantity = 0;
    let total = 0;
    for (let i = 0; i < cash; i++) {
      if (cash > total) {
        quantity++;
        total = price * quantity;
      }
      if (total === cash) {
        return quantity;
      }
      if (total > cash) {
        return quantity - 1;
      }
    }
    return quantity;
  }

  // Buy item with n of qty
  function buyN(item_name, qty) {
    qty = parseInt(qty);
    // console.log(item, qty, priceChart);
    const item = priceChart.items.filter((p) => p.name === item_name)[0];
    const items = priceChart.items;
    const cash = priceChart.cash;
    const bank = priceChart.bank;
    const debt = priceChart.debt;
    const new_item = item;
    if (item.price > cash) {
      setMessage("You dont have enough cash");
      return 0;
    }
    if (new_item) {
      new_item.qty += qty;
    }
    const new_cash = cash - item.price * qty;
    setPriceChart({
      cash: new_cash,
      bank: bank,
      debt: debt,
      items: [...items]
    });

    hide_slider();
  }

  // Sell items & make money
  function sellN(item_name, qty) {
    qty = parseInt(qty);
    const item = priceChart.items.filter((p) => p.name === item_name)[0];
    const items = priceChart.items;
    const cash = priceChart.cash;
    const bank = priceChart.bank;
    const debt = priceChart.debt;

    if (item.qty > 0) {
      // Object.assign(priceChart.items, { : "Pete" });
      item.qty -= qty;
    } else {
      setMessage(`You dont have any ${item_name} left`);
      hide_slider();
      return false;
    }
    const new_cash = cash + item.price * qty;
    setPriceChart({
      cash: new_cash,
      bank: bank,
      debt: debt,
      items: [...items]
    });
    hide_slider();
  }

  // Buy function for + btn
  function buyOne(item_name) {
    buyItem(item_name);
  }

  // Sell function for - btn
  function sellOne(item_name) {
    sellItem(item_name);
  }

  ////////////////////////////////////////////
  // State for managing custom input slider
  // & confirmation box.

  // max quantity of items to be bought or sold
  const [qty_max, setQtyMax] = React.useState(0);

  // slider toggle state
  const [show_slider, setShowSlider] = React.useState(false);

  // confirmation box/ prompt toggle state
  const [showPrompt, setShowPrompt] = React.useState(false);

  // use less maybe
  // const [quant, setQuant] = React.useState(1);
  // anyways dont touch it

  // for setting itemname for input slider & confirmation box
  const [item_name, setItemName] = React.useState("");

  // Buy from slider
  function buyItem(item_name) {
    const item = priceChart.items.filter((p) => p.name === item_name)[0];
    const cash = priceChart.cash;
    const qty = max_qty(item.price, cash);
    if (qty <= 0) {
      setMessage(`You can't buy ${item_name}`);
      return 0;
    }
    setItemName(item_name);
    setAction("Buy");
    setQtyMax(qty);
    setqty(qty);
    setShowSlider(true);
  }

  // Sell from slider
  function sellItem(item_name) {
    const item = priceChart.items.filter((p) => p.name === item_name)[0];
    const qty = item.qty;
    if (qty <= 0) {
      setMessage(`You don't have any ${item_name} left`);
      return 0;
    }
    setAction("Sell");
    setItemName(item_name);
    setQtyMax(qty);
    setqty(qty);
    setShowSlider(true);
  }

  // hide slider function
  function hide_slider() {
    // setQuant(1);
    setShowSlider(false);
  }
  // more states for handling input slider & confirmation box
  const [qty, setqty] = React.useState(qty_max);
  const [action, setAction] = React.useState("");
  const inputRef = React.useRef();

  // function for handling value coming from input slider
  function handleValue(e, name) {
    setqty(inputRef.current.value);
  }

  // function for handling click event
  // coming from pricechart
  // //
  function handleClick(item_name) {
    const item = priceChart.items.filter((p) => p.name === item_name)[0];
    const cash = priceChart.cash;
    // if user can afford to buy then show buy slider
    if (item.qty <= 0) {
      buyItem(item_name);
    }
    //  if user can't afford to buy more then show sell slider
    else if (item.qty > 0 && cash < item.price) {
      sellItem(item_name);
    }
    // if user can buy & sell then show confirmation box
    else if (item.qty > 0) {
      setItemName(item_name);
      setShowPrompt(true);
    }
  }

  // function for handling Confirmation box
  // Refactor it to useReducer or may switch case.
  function handleSetAction(action, item_name) {
    if (action === "buy") {
      setShowPrompt(false);
      buyItem(item_name);
    } else if (action === "sell") {
      setShowPrompt(false);
      sellItem(item_name);
    } else if (action === "deposit") {
      setShowDialog(false);
      handleBankDeposit();
    } else if (action === "withdraw") {
      setShowDialog(false);
      handleBankWithDraw();
    } else if (action === "cancel") {
      setShowDialog(false);
      setDebtShowDialog(false);
    } else if (action === "pay-loan") {
      setShowDialog(false);
      setDebtShowDialog(false);
      handleLoanPay();
    } else if (action === "take-loan") {
      setShowDialog(false);
      setDebtShowDialog(false);
      handleLoanTake();
    } else {
      console.log(action, item_name);
      setShowPrompt(false);
    }
  }

  function handleDeposit(qty) {
    let amount = parseInt(qty);
    const items = priceChart.items;
    const cash = priceChart.cash;
    const bank = priceChart.bank;
    const debt = priceChart.debt;
    if (cash < amount) {
      // console.log(amount);
      hide_slider();
      setQtyMax(priceChart.cash);
      setMessage("You dont have enough cash!!!");
      return false;
    }
    const new_cash = cash - amount;
    const new_bank = bank + amount;
    setPriceChart({
      cash: new_cash,
      bank: new_bank,
      debt: debt,
      items: [...items]
    });
    hide_slider();
  }
  function handleWithdraw(qty) {
    let amount = parseInt(qty);
    const bank = priceChart.bank;
    if (amount > bank) {
      hide_slider();
      setQtyMax(priceChart.bank);
      setMessage("Not enough bank balance!!!");
      return 0;
    }
    const items = priceChart.items;
    const cash = priceChart.cash;
    const debt = priceChart.debt;
    const new_cash = cash + amount;
    const new_bank = bank - amount;
    setPriceChart({
      cash: new_cash,
      bank: new_bank,
      debt: debt,
      items: [...items]
    });
    hide_slider();
  }

  function handleLoanPaid(qty) {
    let amount = parseInt(qty);
    const items = priceChart.items;
    const cash = priceChart.cash;
    const bank = priceChart.bank;
    const debt = priceChart.debt;
    if (debt < 0) {
      setQtyMax(priceChart.debt);
      setMessage("you dont have any debt");
      return false;
    }
    const new_cash = cash - amount;
    const new_debt = debt - amount;
    setPriceChart({
      cash: new_cash,
      bank: bank,
      debt: new_debt,
      items: [...items]
    });
    hide_slider();
  }
  function handleLoanTaken(qty) {
    let amount = parseInt(qty);
    const items = priceChart.items;
    const cash = priceChart.cash;
    const bank = priceChart.bank;
    const debt = priceChart.debt;
    const new_cash = cash + amount;
    const new_debt = debt + amount;
    setPriceChart({
      cash: new_cash,
      bank: bank,
      debt: new_debt,
      items: [...items]
    });
    hide_slider();
  }
  // function for handling action coming from input slider
  function handleAction(item_name, qty, action) {
    if (action === "buy" || action === "Buy") {
      buyN(item_name, qty);
    } else if (action === "sell" || action === "Sell") {
      sellN(item_name, qty);
    } else if (action === "Deposit" || action === "Deposit Cash!") {
      console.log(item_name, "==>", qty);
      handleDeposit(qty);
    } else if (action === "Withdraw" || action === "Withdraw Cash!") {
      console.log(item_name, "==>", qty);
      handleWithdraw(qty);
    } else if (action === "pay-debt" || action === "Pay Debt") {
      console.log(item_name, "==>", qty);
      handleLoanPaid(qty);
    } else if (action === "take-debt" || action === "Pay Debt") {
      console.log(item_name, "==>", qty);
      handleLoanTaken(qty);
    }
    // add take handle take loan here
  }

  return (
    <div className="App">
      <h1 className="status-bar">
        In: {currentArea.replace(/^\w/, (c) => c.toUpperCase())} | CASH: $
        {priceChart.cash} | DAYS LEFT: {days}
      </h1>
      <h2>
        <button
          onClick={() => {
            setDialogContent("Welcome to Citi Bank");
            setShowDialog(true);
          }}
        >
          Bank: ${priceChart.bank}
        </button>
        <button
          onClick={() => {
            setDebtDialogContent("Need Money??");
            setDebtShowDialog(true);
          }}
        >
          DEBT: ${priceChart.debt}
        </button>
      </h2>
      <div className="d_row">
        <h4>Where To?</h4>
        <br />
        {location.map((i, index) => (
          <button
            className={i === currentArea ? "btn-disabled" : "location-btn"}
            key={index}
            onClick={(e) => changeLocation(i, e)}
          >
            {i.replace(/^\w/, (c) => c.toUpperCase())}
          </button>
        ))}
      </div>
      <Alert message={message} hideAlert={hideAlert} />
      {/* TOODS: */}
      {/*  */}
      {/* color coded price indicator Done */}
      {/* Pocket sizes aka quanitity restriction */}
      {/* RPG elements with health & guns & cops */}
      {/* Mugger & robbery */}
      {/* msgs for all time high price & low price */}
      {/*  */}
      <Chart
        data={priceChart}
        buybtn={buyOne}
        sellbtn={sellOne}
        buyItem={buyItem}
        handleClick={handleClick}
      />
      <RangeSlider
        min={1}
        max={qty_max}
        item_name={item_name}
        name={action}
        inputRef={inputRef}
        qty={qty}
        handleValue={handleValue}
        showSlider={show_slider}
        hideSlider={hide_slider}
        handleAction={handleAction}
      />
      <Dialog
        showDialog={showDialog}
        dialogContent={dialogContent}
        setAction={handleSetAction}
      />
      <DebtDialog
        debtShowDialog={debtShowDialog}
        debtDialogContent={debtDialogContent}
        setAction={handleSetAction}
      />

      <ConfirmBox
        setAction={handleSetAction}
        showPrompt={showPrompt}
        item_name={item_name}
      />
    </div>
  );
}
